@import url(https://fonts.googleapis.com/css2?family=Teko:wght@500&display=swap);
html,
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  text-decoration: none !important;
}

.App{background-color:rgba(0,62,193,0.1);height:calc(100vh - 68px);flex-wrap:nowrap;text-align:center;width:100vw}.App .component-container{align-items:flex-start;display:flex;flex-direction:row;flex-wrap:wrap;height:calc(100% - 93px);justify-content:space-evenly;margin-top:68px;padding-top:4%;width:100%}@media (max-height: 1024px) and (max-width: 1366px){.App .component-container{flex-direction:column;align-items:center}}

.navbar-container{background-color:white;border-bottom:1px solid #d1cece;display:flex;flex-direction:row;height:68px;justify-content:space-between;position:absolute;top:0;width:100%;z-index:100}.navbar-container .navbar-title{color:rgba(0,82,255,0.8);font-family:"Teko", sans-serif;font-size:45px;font-weight:600;height:100%;margin:auto 0 auto 40px;letter-spacing:0.02em;padding-top:5px;text-decoration:none;text-align:left}.navbar-container .navbar-navigation{align-items:center;color:rgba(0,82,255,0.8) !important;display:flex;flex-direction:row;font-weight:500;justify-content:space-evenly;margin-right:40px;width:10%}.navbar-container .nav-link{color:rgba(0,82,255,0.8);font-size:1.25vw}.navbar-container .active-nav-link,.navbar-container .nav-link:hover{color:rgba(0,82,255,0.9);font-weight:700}.navbar-container .nav-link,.navbar-container .active-nav-link{transition:0.25s}@media (max-device-height: 812px) and (max-device-width: 375px) and (orientation: portrait){.navbar-container{display:block}.navbar-container .navbar-navigation{background-color:#fff;bottom:calc(-100vh + 68px);height:50px;position:absolute;width:100%}.navbar-container .navbar-navigation .nav-link{font-size:15px}.navbar-container .navbar-title{margin:0;text-align:center}}

.error-container{-webkit-animation:4s warning linear;animation:4s warning linear;border-radius:7px;height:50px;background-color:#dd3535;color:white;font-size:12px;font-weight:700;padding:0 10px;position:absolute;right:15px;top:10px;width:200px;z-index:100}@-webkit-keyframes warning{0%{opacity:0;top:40px}10%{opacity:1;top:10px}80%{opacity:1}100%{opacity:0}}@keyframes warning{0%{opacity:0;top:40px}10%{opacity:1;top:10px}80%{opacity:1}100%{opacity:0}}

.chart-wrapper-container{border:1px solid #d1cece;height:70vh;position:relative;width:70vw}.chart-wrapper-container .timeline-button{border:none;background-color:transparent;color:#b9b9b9;cursor:pointer;font-size:1vw;font-weight:700;transition:0.35s;white-space:nowrap}.chart-wrapper-container .timeline-button:hover{color:rgba(0,82,255,0.8)}.chart-wrapper-container .active-timeline{color:rgba(0,82,255,0.8)}.chart-wrapper-container .coin-button-container{align-items:flex-start;display:flex;flex-direction:row;justify-content:flex-start;padding:15px 25px}.chart-wrapper-container .chart-header-container{display:flex;flex-direction:row;justify-content:space-between;position:absolute;width:100%}.chart-wrapper-container .new-coin-input{border:none;border-bottom:1px solid rgba(0,82,255,0.8);font-weight:700;outline:none;padding:0px 1px;width:60px}.chart-wrapper-container .new-coin-input::-webkit-input-placeholder{color:#b9b9b9}.chart-wrapper-container .new-coin-input:-ms-input-placeholder{color:#b9b9b9}.chart-wrapper-container .new-coin-input::placeholder{color:#b9b9b9}.chart-wrapper-container .new-coin-input:focus{color:rgba(0,82,255,0.8)}.chart-wrapper-container .coin-list-item,.chart-wrapper-container .cancel{background-color:transparent;border:none;cursor:pointer;margin:0;padding:0 5px;text-align:left;transition:0.25s;width:100%}.chart-wrapper-container .cancel{background-color:#dd3535;border-radius:5px;color:#fff;font-weight:600;margin-top:5px;padding:5px;text-align:center;white-space:nowrap;width:65px}.chart-wrapper-container .coin-list-item:hover{background-color:rgba(0,82,255,0.15)}.chart-wrapper-container .coin-search-container{text-align:left;font-size:14px;width:150px;position:absolute}.chart-wrapper-container .remove-button{color:#dd3535 !important}.chart-wrapper-container .positive{color:#13a313}.chart-wrapper-container .negative{color:#dd3535}.chart-wrapper-container .percentage{padding-top:5px;font-weight:400;font-size:1.25vw;padding-left:10px}.chart-wrapper-container .input-container{margin-left:4px;margin-top:-2px}.chart-wrapper-container .coin-chart-header{color:rgba(0,82,255,0.8);font-size:2vw;font-weight:700}.chart-wrapper-container .line-container{background-color:white;border:1px solid #d1cece;border-top:none;height:100%;margin-left:-1px;padding-top:5vh;width:100%}.chart-wrapper-container .current-price{font-weight:500;padding:15px 25px;text-align:left}.chart-wrapper-container .current-price .price-action{padding-top:5px;font-size:1.2vw}.chart-wrapper-container .current-price .price-header{color:gray;font-size:1vw}.chart-wrapper-container .button-container{align-items:flex-start;display:flex;padding:15px 25px}@media (max-device-height: 1366px) and (max-device-width: 1024px) and (orientation: portrait){.chart-wrapper-container{height:25vh}}@media (max-width: 1100px){.chart-wrapper-container{width:90vw}.chart-wrapper-container .coin-chart-header{font-size:18px}.chart-wrapper-container .percentage{font-size:14px;padding:0}.chart-wrapper-container .price-action{font-size:14px !important}.chart-wrapper-container .price-header{font-size:14px !important}.chart-wrapper-container .timeline-button{font-size:14px}}@media (max-device-height: 812px) and (max-device-width: 375px) and (orientation: portrait){.chart-wrapper-container .button-container{margin:0 auto;padding:0}.chart-wrapper-container .chart-header-container{flex-wrap:wrap}.chart-wrapper-container .coin-button-container{margin-top:65%;background-color:transparent}.chart-wrapper-container .current-price{align-items:flex-start;display:flex;flex-direction:row;justify-content:space-between;padding:5px;width:100%}.chart-wrapper-container .current-price .price-action{padding:0}.chart-wrapper-container .current-price .price-header{display:none}}

.news-wrapper .all-news-container{background-color:#fff;border:1px solid #d1cece;overflow:scroll;height:calc(100vh - 200px);position:relative;width:90vw}.news-wrapper .all-news-container .scroll-for-more{color:rgba(0,82,255,0.8);font-weight:700;position:absolute;top:0;width:100%}.news-wrapper .all-news-container .source{color:#b9b9b9;font-size:10px}.news-wrapper .all-news-container:first-child{padding-top:40px}.news-wrapper .home-news-container{height:75vh;display:flex;flex-direction:column;justify-content:space-between}.news-wrapper .home-news-container .empty{height:15%}.news-wrapper .home-news-container .for-you{color:rgba(0,82,255,0.8);font-size:25px;font-weight:700;padding-bottom:5px}.news-wrapper .home-news-container .news-container{background-color:#fff;border:1px solid #d1cece;display:flex;flex-direction:column;justify-content:space-between;width:300px}.news-wrapper .home-news-container .news-container:first-child{padding-top:5px}.news-wrapper .home-news-container .view-more{background-color:rgba(0,82,255,0.8);color:white;font-size:20px;font-weight:500;padding:10px;text-decoration:none}.news-wrapper .news-item-container{align-items:center;color:#000;display:flex;flex-direction:row;justify-content:flex-start;padding:5px 10px;text-align:left;text-decoration:none;transition:0.25s}.news-wrapper .news-item-container .news-image{height:50px;padding-right:10px;width:50px}.news-wrapper .news-item-container .news-title{font-weight:500;font-size:13px;letter-spacing:0.05em;line-height:1.1rem;margin:0;text-transform:full-width}.news-wrapper .news-item-container:hover{background-color:#e9e7e7}@media (max-device-height: 1366px) and (max-device-width: 1024px) and (orientation: portrait){.news-wrapper .home-news-container{display:block !important;height:30vh;width:75vw}.news-wrapper .home-news-container .empty{height:0 !important}.news-wrapper .home-news-container .for-you{font-size:100px !important}.news-wrapper .home-news-container .news-container{flex-direction:row !important;flex-wrap:wrap;width:100% !important}.news-wrapper .home-news-container .news-container>:nth-last-child(2){display:none}.news-wrapper .home-news-container .news-container :nth-last-child(1){flex-grow:2}.news-wrapper .home-news-container .news-item-container{width:45%}}@media (max-width: 1100px){.news-wrapper .home-news-container{display:none}}@media (max-device-height: 812px) and (max-device-width: 375px) and (orientation: portrait){.news-wrapper .home-news-container{margin-top:30%;width:90vw}.news-wrapper .home-news-container .for-you{font-size:16px !important}.news-wrapper .home-news-container .news-container{flex-direction:column !important;align-items:flex-start !important;justify-content:space-evenly !important}.news-wrapper .home-news-container .news-image{height:25px !important;width:25px !important}.news-wrapper .home-news-container .news-item-container{padding:5px 0;width:100%}.news-wrapper .home-news-container .news-item-container .news-title{font-size:10px}.news-wrapper .home-news-container .view-more{width:calc(90vw - 20px)}}

